/* --------- CONTACTS ---------- */
.contacts {
  margin-bottom: 60px;
  /* counter css*/
}
.contacts table {
  counter-reset: section;
}
.contacts table tr td:first-child::before {
  counter-increment: section;
  content: counter(section);
}
.contacts table {
  display: grid;
}
.contacts table tr {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 2rem;
  margin-bottom: 1rem;
  padding: 0.25rem 1rem;
  background-color: rgba(0, 0, 0, 0.05);
  border-radius: 8px;
  background: transparent;
  border-radius: 10px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1), -5px -5px 10px #fff;
}
.contacts table tr td:not(:first-child) {
  flex: 1;
}
.contacts table tr .btn-table {
  opacity: 0;
}
.contacts table tr .btn-table:hover {
  background-color: rgba(0, 0, 0, 0.1);
}
.contacts table tr .btn-table.edit svg {
  fill: #4caf50;
}
.contacts table tr .btn-table.delete svg {
  fill: #f44336;
}
.contacts table tr:hover {
  background-color: rgba(0, 0, 0, 0.03);
}
.contacts table tr:hover .btn-table {
  opacity: 1;
}